<template>
  <div class="stock-register">
    <div>
      <el-form>
        <el-form-item>
          <h2>Register</h2>
        </el-form-item>
        <el-form-item>
          <el-upload
            class="avatar-uploader"
            action="fake"
            :on-error="displayAvatar"
            :show-file-list="false">
            <img
              v-if="userInfo.avatar"
              :src="userInfo.avatar"
              class="avatar">
            <i
              v-else
              class="el-icon-plus avatar-uploader-icon">
            </i>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="userInfo.user_name"
            placeholder="Please Input Username">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="userInfo.password"
            placeholder="Please Input Password"
            show-password>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="success"
            @click="register">
            Register
          </el-button>
          <el-button
            @click="login">
            Login
          </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { ElForm, ElFormItem, ElInput, ElButton, ElUpload } from 'element-plus'
import { ROUTE_LOGIN, ROUTE_REGISTER } from '@/router/config'
import { register_api, upload_api } from '@/store/api'
import axios from 'axios'
export default defineComponent({
  components: {
    ElForm,
    ElFormItem,
    ElInput,
    ElButton,
    ElUpload,
  },
  setup(props, { attrs, slots, emit }) {
    const userInfo = ref({
      user_name: '',
      password: '',
      avatar: '',
    })
    // 头像文件
    let avatarFile = ''
    const register = async () => {
      const form = new FormData();
      form.append('file', avatarFile);
      const avatarRes = await axios.post(upload_api, form, {
        headers: { 'Content-Type': 'multypart/form-data' },
      })
      userInfo.value.avatar = avatarRes.data.data.path
      const registerRes = await axios.post(register_api, userInfo.value)
      const registerSuccess = registerRes.data.code === 1000
      if(registerSuccess) {
        login()
      }
    }
    const router = useRouter()
    const login = () => {
      router.push(ROUTE_LOGIN.use())
    }
    const displayAvatar = (_, file) => {
      avatarFile = file.raw
      userInfo.value.avatar = URL.createObjectURL(file.raw);
    }
    return {
      userInfo,
      login,
      register,
      displayAvatar,
    }
  }
})
</script>

<style lang="scss">
  .stock-register {
    max-width: 250px;
    margin: 0 auto;
    text-align: center;
    background-color: #fff;
    padding: 10px 100px;
    box-shadow: var(--shadow);
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
</style>
